





























































































































#map svg {
  background-color: #c9dbe9;
  border: 4px solid black;
  position: relative;
}
.country {
}
.country.visited {
  fill: #3f51b5;
}
.country.visited:hover {
  fill: #283593;
}
.country.unvisited {
  fill: #9e9e9e;
}
.country.unvisited:hover {
  fill: #424242;
}
.country:hover {
  stroke: #fff;
  stroke-width: 1.5px;
}
.legend .legend-item {
}
.legend .legend-item.visited {
  fill: #3f51b5;
  stroke: #283593;
}
.legend .legend-item.unvisited {
  fill: #9e9e9e;
  stroke: #424242;
}
.legend .legend-item-text {
  font-style: normal
}
.legend .legend-item-text.visited {
  fill: #283593;
}
.legend .legend-item-text.unvisited {
  fill: #424242;
}
.hidden {
  display: none;
}
div.tooltip {
  color: #222; 
  background: #fff; 
  padding: .5em; 
  text-shadow: #f5f5f5 0 1px 0;
  border-radius: 2px; 
  box-shadow: 0px 0px 2px 0px #a6a6a6; 
  opacity: 0.9; 
  position: absolute;
  z-index: 999;
}



